  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: auto 100%;
}

/*content placement*/
#mainContent {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  padding-left: 4vw;
  padding-right: 4vw;
}

#todoControls {
}

.todoControls {
  display: flex;
  flex-direction: column;
  padding: calc(60px);
  padding-top: 30px;
}

#todoTools,
#todoActions {
  justify-content: center;
  align-items: center;
}

#todoOverview > * {
  overflow: auto;
}

/*sizing*/
#todoControls {
  width: 35%;
}

#todoTools,
#todoActions {
  min-width: 352px;
  max-width: 352px;

  height: 460px !important;
}

#todoOverview {
  max-width: 90%;
  max-height: 95vh;
  margin-left: 50px;
  width: 100%;
}

#todoOverview > * {
  height: 100%;
  width: 100%;
  min-height: 200px;
}

#topbarContainer {
  width: 100%;
  height: 100%;
}

#sidebarContainer {
  width: 100%;
  height: 100%;
}

.topbar {
  padding-left: 1em;
  height: 1em;
  width: auto;
}

.sidebar {
  width: 1em;
  height: 30%;
}

.lowersection {
  display: flex;
  height: 93%;
  align-items: center;
}

/*backgrounds*/
#todoTools {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: 100% 100%;
}

#todoActions {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: 100% 100%;
}

/*container-relative placements*/
#todoOverview {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto 1fr;
  gap: 10px;
  align-items: start;
  justify-items: start;
}

#todoOverview {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 30px; /* optional spacing */
  padding: 0px;
}

#topbarContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

#topbar {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

#sidebarContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

#sidebar {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

/*media queries*/
@media only screen and (max-width: 1000px) {
  #todoControls {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  #mainContent {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    align-items: center;
  }

  #todoOverview {
    margin-left: 0px;
    margin-top: 30px;
  }

  #todoOverview > * {
    min-width: 300px;
  }
}

/*detail formatting*/
.todoControls > * :not(label) {
  border: 1px solid rgb(87, 85, 85);
  border-radius: 0.2em;
}

#todoOverview > * {
  border: 2px dashed grey;
}

button {
  font-weight: 700;
  font-size: medium;
}

#sidebar > * {
  text-orientation: upright;
}

.sidebar {
  width: 1em;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
}

.lowersection {
  display: flex;
  align-items: center;
}

.bar {
  text-align: center;
  font-weight: 700;
  font-size: medium;
  font-family: Arial, Helvetica, sans-serif;
}

